body.lb-disable-scrolling {
  overflow: hidden;
}

.lightboxOverlay {
  position: fixed;
  overflow: hidden;
  
  top: 0;
  left: 0;
  z-index: 9999;
  background-color: black;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=80);
  opacity: 0.8;
  display: none;
  height: 100vh !important;

}

.lightbox {
  position: absolute;
  left: 0;
  width: calc(100% - 320px);
  z-index: 10000;
  text-align: center;
  line-height: 0;
  font-weight: normal;
  outline: none;
}
body.right-comment-none .lightbox {width: 100% !important;}
body.right-comment-none .right-comment {display: none;}

.lightbox .lb-image {
  display: block;
  height: auto;
  max-width: inherit;
  max-height: none;
  border-radius: 3px;

  /* Image border */
  /*border: 4px solid white;*/
}

.lightbox a img {
  border: none;
}

.lb-outerContainer {
  position: relative;
  *zoom: 1;
  width: 250px;
  height: 250px;
  margin: 0 auto;
  border-radius: 4px;

  /* Background color behind image.
     This is visible during transitions. */
  background-color: white;
}

.lb-outerContainer:after {
  content: "";
  display: table;
  clear: both;
}

.lb-loader {
  position: absolute;
  top: 43%;
  left: 0;
  height: 25%;
  width: 100%;
  text-align: center;
  line-height: 0;
}

.lb-cancel {
  display: block;
  width: 32px;
  height: 32px;
  margin: 0 auto;
  background: url(data:image/gif;base64,) no-repeat;
}

.lb-nav {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 10;
}

.lb-container > .nav {
  left: 0;
}

.lb-nav a {
  outline: none;
  background-image: url('data:image/gif;base64,R0lGODlhAQABAPAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==');
}

.lb-prev, .lb-next {
  height: 100%;
  cursor: pointer;
  display: block;
}

.lb-nav a.lb-prev {
  width: 34%;
  left: 0;
  float: left;
  background: url(data:image/png;base64,) left 48% no-repeat;
  /*filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);*/
  opacity: 0;
  -webkit-transition: opacity 0.6s;
  transition: opacity 0.6s;
}

.lb-nav a.lb-prev:hover {
  /*filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);*/
  opacity: 1;
}

.lb-nav a.lb-next {
  width: 64%;
  right: 0;
  float: right;
  background: url(data:image/png;base64,) right 48% no-repeat;
  /*filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);*/
  opacity: 0;
  -webkit-transition: opacity 0.6s;
  transition: opacity 0.6s;
}

.lb-nav a.lb-next:hover {
  /*filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);*/
  opacity: 1;
}

.lb-dataContainer {
  margin: 0 auto;
  padding-top: 5px;
  *zoom: 1;
  width: 100%;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.lb-dataContainer:after {
  content: "";
  display: table;
  clear: both;
}

.lb-data {
  padding: 0 4px;
  color: #ccc;
}

.lb-data .lb-details {
  width: 85%;
  float: left;
  text-align: left;
  line-height: 1.1em;
}

.lb-data .lb-caption {
  font-size: 13px;
  font-weight: bold;
  line-height: 1em;
}

.lb-data .lb-caption a {
  color: #4ae;
}

.lb-data .lb-number {
  display: block;
  clear: left;
  padding-bottom: 1em;
  font-size: 12px;
  color: #999999;
}

.lb-data .lb-close {
  display: block;
  float: right;
  width: 20px;
  height: 20px;
  background: url(data:image/png;base64,) top right no-repeat;
  text-align: right;
  background-size: contain;
  outline: none;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=70);
  opacity: 0.7;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}

.lb-data .lb-close:hover {
  cursor: pointer;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
}

@media (max-width: 1200px) {
  .lightbox .lb-image,.lb-outerContainer {width: 100% !important; height: 100% !important; }
}
@media (max-width: 768px) {
  .lightbox {top:0 !important; width: 100% !important;}
  .lightbox .lb-image,.lb-outerContainer {width: 100% !important; }
  .lb-dataContainer  {position: absolute; top:0; z-index: 10;}
  .right-comment {top:auto !important; width: 100% !important; padding-bottom: 30px; height: 70% !important;}
  .lightboxOverlay {background-color: #fff; opacity: 1 !important;}
  .lb-data .lb-number {color: #fff;}
}

body {
	text-align: right;
}
.text-left {
	text-align: right !important;
}
.text-right {
	text-align: left !important;	
}
.float-left {
	float: right !important;
}

.float-right {
	float: left !important;
}
.right-addon i,
.right-addon img {
    left: 0;
    right:auto;
}

.left-addon i,
.left-addon img {
    right: 0;
    left:auto;
}

.panel .panel-heading .panel-title .accordion-toggle:after {
	right:auto;
	left:-60px;
}
.panel .panel-heading .panel-title {
	padding-left: 50px;
	padding-right: 0; 
}

.ad-box.fixed-box {
	left:auto;
	margin-left: auto;
	right:50%;
	margin-right: -150px; 
}

.modal.bottom .modal-content .close {
	right:auto;
	left:10px; 
}

.timeline {
	padding-left: 0;
	padding-right: 40px;
}
.timeline li:before{
	left:auto;
	right: -38px; 
	top:2px;
	z-index: 401;
}
.timeline li:after {
	left:auto;
	right:-32px;
}

.list-group .list-group-item .badge {
	right:auto;
	left:0;
}

.modal.left .modal-content .close,
.modal.right .modal-content .close {
	right:auto;
	left:10px;
}

.app-header.right-div:after {
	right:auto;
	left:-30px;
}
.app-header.right-div:before {
	right:auto;
	left:-17px;
}

.app-header .menu-icon .cart-count {
	right:auto;
	left:4px;
}

.product-tag {left:auto; right:10px;} 
.live-tag,
